import Vue from 'vue';

Vue.filter('tab', function(code){
  const companyTypeArr = [
          '2',
          '',
          '',
          '',
          ''
      ],
      codeArr = [1, 2, 3];
  const index = codeArr.indexOf(code);
  return companyTypeArr[index];
});

